import React, { Component } from "react"
import { Row, Col, Button, Menu, Dropdown, Icon } from "antd"
import { Link } from "react-router-dom"

class Auth extends Component {
  constructor() {
    super()
    this.state = {
      nodes: [],
      activeNode: null
    }
  }

  componentDidMount() {
    const thiss = this
    fetch("https://223421.selcdn.ru/Eruption-dev/nodes/list.json")
      .then(response => {
        response.json().then(data => {
          thiss.setState({
            nodes: data
          })
        })
      })
      .catch(() => {
        // console.log(err)
      })
  }

  selectNode(node) {
    this.setState({
      activeNode: node.item.props.data
    })
    // console.log(node.item.props.data)
    localStorage.setItem("node", JSON.stringify(node.item.props.data))
  }

  render() {
    const menu = (
      <Menu onClick={this.selectNode.bind(this)}>
        {this.state.nodes.map((node, i) => {
          return (
            <Menu.Item key={i} data={node}>
              {node.Name}
            </Menu.Item>
          )
        })}
        {
          // this.state.activeNode ? <Menu.Item key='auto' data={null}></Menu.Item> : null
        }
      </Menu>
    )
    return (
      <Row
        type="flex"
        align="middle"
        justify="center"
        style={{ height: "100vh" }}
      >
        <Col>
          <div className="auth__type">
            <h1></h1>
            <h2>  </h2>
            <Link to={this.props.match.url + "/by-phone"}>
              <Button type="secondary">  </Button>
            </Link>
            <br />
            <Link to={this.props.match.url + "/by-email"}>
              <Button type="secondary">  </Button>
            </Link>
            <br />
            <Button type="secondary"> </Button>
            <br />
            <br />
            <Link to={"/registration"}></Link>
            <br />
            <div className="auth__type_server">
              <span>: </span>
              <Dropdown overlay={menu}>
                <span className="ant-dropdown-link">
                  {localStorage.getItem("node")
                    ? JSON.parse(localStorage.getItem("node")).Name
                    : ""}
                  <Icon type="down" />
                </span>
              </Dropdown>
            </div>
          </div>
        </Col>
      </Row>
    )
  }
}

// Auth.defaultProps = {
//    img: 'https://223421.selcdn.ru/chatix-dev/assets/img/default_avatar.png'
// }

export default Auth
